import { Field, ObjectType } from "@nestjs/graphql";

@ObjectType()
export class PaginateMeta {

  @Field(() => Number)
  itemsPerPage: number;

  @Field(() => Number)
  totalItems: number;
  
  @Field(() => Number)
  currentPage: number;
  
  @Field(() => Number)
  totalPages: number;
  
  /* @Field(() => [String])
  sortBy: string[]; */
  
  @Field(() => [String], { nullable: true })
  searchBy: string[];

  @Field(() => String, { nullable: true })
  search: string;

  /* @Field(() => [[String]], { nullable: true })
  filter?: {
      [column: string]: string | string[];
  }; */
}